import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useAdmissionExportExcelList } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import AdmissionExportCard from "./AdmissionExportCard";
import AdmissionExportFilter from "./AdmissionExportFilter";
const AdmissionExport = ({ aid, fid, instituteId }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [openStep, setOpenStep] = useState("");
  const {
    admissionExportExcelList,
    admissionExportExcelListLoading,
    admissionExportExcelListRefetch,
  } = useAdmissionExportExcelList({
    data: {
      aid: aid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !aid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionExportExcelList?.list?.length) {
      let obj = nextPage(admissionExportExcelList?.list?.length);
      setState(() => obj);
    }
  }, [admissionExportExcelList?.list?.length]);
  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onRefetch = useCallback(() => {
    setPage((prev) => (prev = 1));
    admissionExportExcelListRefetch();
  }, [admissionExportExcelListRefetch]);

  const onClose = () => {
    setOpenStep("");
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("data_export")}</h6>
            <div className={style.fm_both_filter}>
              <div
                onClick={() => setOpenStep("TRANSACTION")}
                className={style.fm_both_filter_with_name}
              >
                <img
                  src={`${assestsUserFinanceUrl}/filter.svg`}
                  alt="filter icon"
                />
              </div>
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <div className={style.fm_search_full}>
            <section className={style.fm_search_full_container}>
              <div className={style.fm_search_full_container_inner}>
                <img
                  className={style.fm_search_full_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                />
              </div>
            </section>
          </div>

          {admissionExportExcelList?.list?.map((excel, index) =>
            admissionExportExcelList?.list?.length === index + 1 ? (
              <div key={excel?._id} ref={ref}>
                <AdmissionExportCard
                  excel={excel}
                  aid={aid}
                  onRefetch={onRefetch}
                />
              </div>
            ) : (
              <AdmissionExportCard
                excel={excel}
                key={excel?._id}
                aid={aid}
                onRefetch={onRefetch}
              />
            )
          )}
          {admissionExportExcelListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            admissionExportExcelList?.list?.length === 0 && (
              <EmptyMessage
                title="No excel found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            admissionExportExcelList?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No excel added."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>

      {openStep && (
        <AdmissionExportFilter
          aid={aid}
          fid={fid}
          instituteId={instituteId}
          onClose={onClose}
        />
      )}
    </>
  );
};

export default AdmissionExport;
