import { useCallback, useState } from "react";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/Income/Income.module.css";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../Ui/Tab/Tile/TileCard";
import {
  batchLabelAdmission,
  batchLabelAdmission1,
  departmentLabel1,
} from "../../../Constant/Statistics/statistics_constant";
import FeeStatisticsDepartmentFilter from "../../FinanceManager/FeeStatistics/FeeStatisticsDepartmentFilter";
import FeeStatisticsBatchFilter from "../../FinanceManager/FeeStatistics/FeeStatisticsBatchFilter";
import FeeStatisticsStandardFilter from "../../FinanceManager/FeeStatistics/FeeStatisticsStandardFilter";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import { useAdmissionExportFilterExcel } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import SelectFeeCategory from "../../FinanceManager/FeesStructure/FeeMaster/SelectFeeCategory";
import { casteCategory } from "../../../../constant/constant_value";
const AdmissionExportFilter = ({ aid, fid, onClose, instituteId }) => {
  const { t } = useTranslation();
  const [nextScreen, setNextScreen] = useState(false);
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    depart: "",
    departType: "",
    batch: "",
    batchType: "",
    master: "",
    fee_struct: "",
    gender: "",
    caste: "",
    is_all: "",
  });
  const [admissionExportFilterExcel] = useAdmissionExportFilterExcel();
  const onDepartmentTile = useCallback((val) => {
    setState((prev) => ({
      ...prev,
      departType: val,
    }));
  }, []);
  const onBatchTile = useCallback((val) => {
    setState((prev) => ({
      ...prev,
      batchType: val,
    }));
  }, []);
  const onSelectDepartment = useCallback((val) => {
    setState((prev) => ({
      ...prev,
      depart: val,
    }));
  }, []);
  const onSelectBatch = useCallback((val) => {
    setState((prev) => ({
      ...prev,
      batch: val,
    }));
  }, []);
  const onSelectStandard = useCallback((val) => {
    setState((prev) => ({
      ...prev,
      master: val,
    }));
  }, []);
  const onGenderSelect = useCallback((val) => {
    setState((prev) => ({
      ...prev,
      gender: val,
    }));
  }, []);
  const onCatseSelect = useCallback((val) => {
    setState((prev) => ({
      ...prev,
      caste: val,
    }));
  }, []);
  const onPendingSelect = useCallback((val) => {
    setState((prev) => ({
      ...prev,
      is_all: val === "All Time Fees" ? true : false,
    }));
  }, []);
  const onCategorySelect = useCallback((val) => {
    setState((prev) => ({
      ...prev,
      fee_struct: val,
    }));
  }, []);
  const onExport = () => {
    if (aid) {
      setDisabled((pre) => !pre);
      admissionExportFilterExcel({
        aid: aid,
        gender: state.gender,
        caste: state.caste,
        is_all: state.is_all,
        all_depart: state.departType,
        batch_status: state.batchType,
        exportData: {
          depart: state.depart,
          batch: state.batch,
          master: state.departType === "All" ? [] : [state.master],
          fee_struct: state.fee_struct,
        },
      })
        .then(() => {
          onClose();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6> {t("filter_and_export_fees_data")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            {!nextScreen ? (
              <>
                <TileWrapper label="department_type">
                  {departmentLabel1?.map((tile) => (
                    <TileCard
                      key={tile?.key}
                      tile={tile}
                      onTile={onDepartmentTile}
                      activeTile={state.departType}
                    />
                  ))}
                </TileWrapper>

                {state.departType === "PARTICULAR" ? (
                  <FeeStatisticsDepartmentFilter
                    fid={fid}
                    onSelect={onSelectDepartment}
                    onlyDepartment
                  />
                ) : null}
                {state.departType === "ALL" ? (
                  <TileWrapper label="batch_type">
                    {batchLabelAdmission1?.map((tile) => (
                      <TileCard
                        key={tile?.key}
                        tile={tile}
                        onTile={onBatchTile}
                        activeTile={state.batch_status}
                      />
                    ))}
                  </TileWrapper>
                ) : (
                  <TileWrapper label="batch_type">
                    {batchLabelAdmission?.map((tile) => (
                      <TileCard
                        key={tile?.key}
                        tile={tile}
                        onTile={onBatchTile}
                        activeTile={state.batch_status}
                      />
                    ))}
                  </TileWrapper>
                )}
                {state.batchType === "PARTICULAR_BATCH" ? (
                  <FeeStatisticsBatchFilter
                    did={state.depart?._id}
                    onSelect={onSelectBatch}
                    onlyBatch
                  />
                ) : null}
                {state.departType === "ALL" ? null : (
                  <FeeStatisticsStandardFilter
                    did={state.depart?._id}
                    onSelect={onSelectStandard}
                    instituteId={instituteId}
                  />
                )}
                <CreateButton
                  label={"next"}
                  onAction={() => setNextScreen((pre) => !pre)}
                />
              </>
            ) : (
              <>
                <CustomSelect
                  selectLabel={t("select_gender")}
                  selectedValue={t("select_gender_placeholder")}
                  options={["Male", "Female", "Other"]}
                  onClick={onGenderSelect}
                  defalutValue={state.gender}
                />
                <CustomSelect
                  selectLabel={t("select_caste_catgory")}
                  selectedValue={t("select_caste_catgory_placeholder")}
                  options={casteCategory}
                  onClick={onCatseSelect}
                  defalutValue={state.caste}
                />
                <SelectFeeCategory
                  fid={fid}
                  onSelect={onCategorySelect}
                  defalutValue={state.category_master?.category_name}
                />
                <CustomSelect
                  selectLabel={t("select_all_or_pending")}
                  selectedValue={t("select_all_or_pending_placeholder")}
                  options={["All Time Fees", "Pending Fees"]}
                  onClick={onPendingSelect}
                  defalutValue={state.is_all}
                />
                <CreateButton label={"export_data"} onAction={onExport} />
              </>
            )}
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AdmissionExportFilter;
