import { useState } from "react";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import FinanceExportEdit from "../../FinanceManager/FinanceExport/FinanceExportEdit";
import {
  useAdmissionEditExportExcel,
  useAdmissionRemoveExportExcel,
} from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";

const AdmissionExportMenu = ({ onClose, xlsxId, aid, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [admissionRemoveExportExcel] = useAdmissionRemoveExportExcel();
  const [admissionEditExportExcel] = useAdmissionEditExportExcel();

  const onDelete = () => {
    if (aid && xlsxId?._id) {
      setDisabled((pre) => !pre);
      admissionRemoveExportExcel({
        aid: aid,
        xlsxId: xlsxId?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };
  const onEdit = (val) => {
    if (aid && xlsxId?._id) {
      setDisabled((pre) => !pre);
      admissionEditExportExcel({
        aid: aid,
        xlsxId: xlsxId?._id,
        excelUpdate: {
          excel_file_name: val,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return edit ? (
    <FinanceExportEdit
      onClose={onOpenEdit}
      onEditAction={onEdit}
      xlsxId={xlsxId}
    />
  ) : (
    <ModalMenu onClose={onClose} disabled={disabled}>
      <h6 onClick={onOpenEdit}>{t("edit")}</h6>
      <h6 onClick={onDelete}>{t("delete")}</h6>
    </ModalMenu>
  );
};

export default AdmissionExportMenu;
