import { useFinanceAllFeesCategory } from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import CustomSelectCategory from "../../../../Ui/Select/CustomSelectCategory";
import { useTranslation } from "react-i18next";

const SelectFeeCategory = ({ defalutValue, onSelect, fid }) => {
  const { t } = useTranslation();
  const { financeAllFeesCategory } = useFinanceAllFeesCategory({
    fid: fid,
    skip: !fid,
  });
  return (
    <CustomSelectCategory
      selectLabel={t("select_fee_category_master")}
      selectedValue={t("please_select_fees_category")}
      options={financeAllFeesCategory?.length > 0 ? financeAllFeesCategory : []}
      onClick={onSelect}
      viewAs="NORMAL_FILTER"
      defalutValue={defalutValue}
      isFull
    />
  );
};

export default SelectFeeCategory;
